.block {
  border: 2px solid black;
  position: relative;

  p {
    margin: 0;
    padding: 0;
    &.text_abs {
      position: absolute;
      top: 25px;
      left: -30px;
      z-index: 20;
      text-align: center;
    }
  }
  &.start-end {
    border-radius: 100%;
    background-color: lightgreen;
    padding: 10px;
  }
  &.normal {
    background-color: lightgreen;
    padding: 10px;
  }
  &.condition {
    padding: 0;
    width: 0; 
    height: 0;
    border: 50px solid transparent;
    border-bottom-color: lightgreen;
    position: relative;
    top: -50px;
  }
  &.condition:after {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    left: -50px;
    top: 50px;
    border: 50px solid transparent;
    border-top-color: lightgreen;
  }

  &.answer_two {
    background-color: lightgreen;
    padding: 10px;
    margin-right: auto;

    .line {
      height: 100px;
      position: absolute;
      top: -103px;
      left: 50px;
      width: 2px;
      background-color: black;
    }

    .hor_line {
      height: 2px;
      position: absolute;
      top: -103px;
      left: 50px;
      width: 50px;
      background-color: black;
    }

    .line_down {
      height: 20px;
      width: 2px;
      background-color: #000;
      position: absolute;
      top: 40px;
      left: 100px;
    }
  }
  &.answer_one {
    background-color: lightgreen;
    padding: 10px;
    margin-left: auto;
    .line {
      height: 102px;
      position: absolute;
      top: -103px;
      right: 50px;
      width: 2px;
      background-color: black;
    }

    .line_down {
      height: 20px;
      width: 2px;
      background-color: #000;
      position: absolute;
      top: 40px;
      right: 80px;
    }

    .hor_line {
      height: 2px;
      position: absolute;
      top: -103px;
      right: 50px;
      width: 50px;
      background-color: black;
    }
  }
}

.condition_answer {
  display: flex;
  width: 200px;
  margin-top: 30px;
  justify-content: space-between;
}

.arrow {
  height: 30px;
  width: 2px;
  background-color: #000;
  z-index: 100;
  &:last-child {
    background-color: transparent;
  }
}

.border {
  margin-top: 20px;
}