.block_items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 20px;
  width: 300px;
}

.btn {
  margin-bottom: 20px;
  padding: 10px;
  background-color: blue;
  color: #fff;
  width: 80px;
  cursor: pointer;
  z-index: 10;
  font-size: 20px;
  border: none;
  &:disabled {
    background-color: gray;
  }
}
